import {createContext} from "react";
import { IProduct } from "./UserContext";
import { Dispatch } from "react";

export type CatalogueContextType = {
    catalogue: Array<IProduct> | [],
    setCatalogue: Dispatch<IProduct>,
}

export type FilterContextType = {
  filteredTitle: Array<IProduct> | [],
  setFilteredTitle: Dispatch<IProduct>,
}

export interface IUser {
    id: string,
    name: string | null,
    favProducts?: Array<IProduct | []>,
    cart?: Array<IProduct | []>,
  }


export const CatalogueContext = createContext<null | CatalogueContextType>(null);
export const FilterContext = createContext<null | FilterContextType>(null);