import React, { createContext } from "react";
import { Dispatch } from "react";
import { UserCredential } from "firebase/auth";
import {User} from "firebase/auth";

export interface IProduct {
    id: string,
    title: string,
    image: string,
    description: string,
    price: string | any,
    category: string,
    counter?: number | any,
}

export type PropsProduct = {
    pProduct: {
        id: string,
        title: string,
        image: string,
        description: string,
        price: string,
        category: string,
        counter?: number | any,
    }

}

