import React from "react";
//@ts-ignore
import style from "./Services.module.css";
//@ts-ignore
import Bin from "../../img/musor.svg";
//@ts-ignore
import BrownBin from "../../img/bin-brown.svg";
//@ts-ignore
import Like from "../../img/heart.svg";
//@ts-ignore
import RedLike from "../../img/heart2.svg";
//@ts-ignore
import BrownLike from "../../img/heart-brown.svg";
import { IProduct, UserContext } from "../../utiles/UserContext";
import { CatalogueContext } from "../../utiles/CatalogueContext";
import { useContext, useEffect, useState } from "react";
import {User, UserCredential} from "firebase/auth";
import { UserData, EditData} from "../../utiles/buttonTypes";
import { FavsContext } from "../../utiles/FavsContext";
import { CartContext } from "../../utiles/cartActions";
import Loader from "../../utiles/Loader/Loader";
//@ts-ignore
//@ts-ignore
import ArrowRight from "../../img/arrow-right.svg";
//@ts-ignore
import ArrowLeft from "../../img/arrow-left.svg";

const Services:React.FC<UserData & EditData> = (props) => {
    const {writeUserData, editUserData} = props;
    const {catalogue, setCatalogue} = useContext<any>(CatalogueContext);
    const {user, setUser} = useContext<any | User | UserCredential>(UserContext);
    const {toggleFavs} = useContext<any>(FavsContext);
    const {deleteFromCart, removeOneQuantity, getToCart} = useContext<any>(CartContext);
    const [loading, setLoading] = useState<boolean>(true);
    const [offset, setOffset] = useState<number>(0);
    const [offsetRecom, setOffsetRecom] = useState<number>(0);
    let counter: number;
    const itemWidth = 288;

    const getCounter = (id: string) => {
        if(user?.cart.some((i: IProduct) => {return i.id === id})){
            user?.cart.find((i: IProduct) => {
                if(i.id === id){
                    return counter = i.counter;
                }
            })
        }else{
            counter = 0;
        }
    }

    const dragToTheLeft = () => {
        setOffset((currentOffset) => {
            const newOffset = currentOffset - itemWidth
            const maxOffset = -(itemWidth * (user?.favProducts?.length - 3));
            return Math.max(newOffset, maxOffset);
        });
    }

    const dragToTheRight = () => {
        setOffset((currentOffset) => {
            const newOffset = currentOffset + itemWidth
            return Math.min(newOffset, 0);
        });
    }

    const dragToTheLeftRecom = () => {
        setOffsetRecom((currentOffset) => {
            const newOffset = currentOffset - itemWidth
            const maxOffset = -(itemWidth * (catalogue?.length - 4));
            return Math.max(newOffset, maxOffset);
        });
    }
    const dragToTheRightRecom = () => {
        setOffsetRecom((currentOffset) => {
            const newOffset = currentOffset + itemWidth
            return Math.min(newOffset, 0);
        });
    }

    useEffect(() => {
        setLoading(false);
    }, [])

    if (loading) {
        return (
            <main style={{
                minHeight: '70vh',
                height: 'fit-content',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center'}}>
                <div><Loader/></div> 
            </main> 
        )
        }else return (
        <main className={style.main}>
            <div className={style.container}>

                <h3>Favourites</h3>      
            {user && user?.favProducts.length > 0 
            ?
                <div className={style.cartContainer}>
                    <div className={style.window}>
                        <div style={{
                            transform: `translateX(${offset}px)`
                            }}
                            className={style.itemsContainer}>


                                {user?.favProducts.map((item: IProduct, i: string) => {
                                console.log(item);
                                const {id, title, price, description: desc, category: cat, image} = item;
                                getCounter(id);
                                return  <div key={id} className={style.cartItem}>
                                            <div className={style.img}><img src={image} alt="product" /></div>
                                            <h6 className={style.cartTitle}>{title}</h6>
                                            <p className={style.cartDesc}></p>
                                            <div className={style.buttons}>
                                                <div onClick={() =>{toggleFavs(id)}}className={style.fav}><img src={user?.favProducts?.some((i: IProduct) => {return i.id === id}) ? RedLike : Like} alt="like" /></div>
                                                <div onClick={() =>{removeOneQuantity(id)}}  className={style.minus}>-</div>
                                                <div className={style.counter}>{counter}</div>
                                                <div onClick={() =>{getToCart(id)}} className={style.plus}>+</div>
                                                <div onClick={() =>{deleteFromCart(id)}} className={style.remove} ><img src={Bin} alt="bin" /></div>
                                            </div>
                                    </div>
                            })}
                            

                        </div>

                    </div>
                    <div onClick={() =>{dragToTheLeft()}} className={style.arrow}><img src={ArrowRight} alt="arrow" /></div>
                    <div onClick={() =>{dragToTheRight()}} className={style.arrowRight}><img src={ArrowLeft} alt="arrow" /></div>
            
            
            </div>
            : <h3>No favs :C</h3>
            }
            <div className={style.recommendContainer}>
            <h3>Also buy</h3>
            <div onClick={() =>{dragToTheLeftRecom()}} className={style.arrow}><img src={ArrowRight} alt="arrow" /></div>
            <div onClick={() =>{dragToTheRightRecom()}} className={style.arrowRight}><img src={ArrowLeft} alt="arrow" /></div>
                <div className={style.recWindow}>
                
                    <div style={{
                            transform: `translateX(${offsetRecom}px)`
                        }}  
                        className={style.recommendItems}>

                        {catalogue && catalogue.length > 0 
                        ?
                            catalogue?.map((item: IProduct, i: string) => {
                            const {id, title, price, description: desc, category: cat, image, counter: count} = item;
                            getCounter(id);
                            return  <div key={id} className={style.cartItem}>
                                        <div className={style.img}><img src={image} alt="product" /></div>
                                        <h6 className={style.cartTitle}>{title}</h6>
                                        <p className={style.cartDesc}></p>
                                        <div className={style.buttons}>
                                            <div onClick={() =>{toggleFavs(id)}}className={style.fav}><img src={user?.favProducts?.some((i: IProduct) => {return i.id === id}) ? RedLike : BrownLike} alt="like" /></div>
                                            <div onClick={() =>{removeOneQuantity(id)}}  className={style.minus}>-</div>
                                            <div className={style.counter}>{counter}</div>
                                            <div onClick={() =>{getToCart(id)}} className={style.plus} >+</div>
                                            <div onClick={() =>{deleteFromCart(id)}} className={style.remove} ><img src={BrownBin} alt="bin" /></div>
                                        </div>
                                </div>       
                        }) 
                        : <h3>No recommendations</h3>
                        }
                        </div>
                    </div>
                </div>
            </div>    
        </main>
    )
}

export default Services;