
import React, { useContext, useEffect } from "react";
import { CatalogueContext } from "../../../utiles/CatalogueContext";
import { UserContext } from "../../../utiles/UserContext";
import { IProduct } from "../../../utiles/UserContext";
import { UserCredential, User } from "firebase/auth";
import { CartContext } from "../../../utiles/cartActions";
//@ts-ignore
import style from "./Product.module.css";
//@ts-ignore
import Like from "../../../img/heart-brown.svg";
//@ts-ignore
import RedLike from "../../../img/heart2.svg";
import { useParams } from "react-router";
import { FavsContext } from "../../../utiles/FavsContext";

const Product: React.FC = () => {
    const {catalogue} = useContext<any>(CatalogueContext);
    const {user} = useContext<any | User | UserCredential>(UserContext); 
    const {toggleFavs} = useContext<any>(FavsContext);
    const {getToCart} = useContext<any>(CartContext);
    let counter: number;
    console.log(user);

    let {id} = useParams();
    let productId: string | null;
    id !== undefined ? productId = id.slice(1) : productId = null;

    const product = catalogue.find((item: IProduct, i: string) => {
        let idStr = item.id.toString();
        if(idStr === productId){
           return item; 
        }
    })

    if(user?.cart.some((i: IProduct) => { return i.id === id})){
        counter = user?.cart.find((i: IProduct) => {
                if(i.id === productId){
                    return counter = i.counter;
                }
            })
    }else{
        counter = 0;
    }
    
    return (
        <main className={style.main}>
            <div className={style.productsContainer}>
                <h3>{product?.title}</h3>
                <div className={style.productItem}>
                                <div className={style.img}><img src={product?.image} alt="" /></div>
                                <div className={style.productInfo}>
                                    <h5>{product?.title}</h5>
                                    <p className={style.productDesc}>{product?.description}</p>
                                    <p className={style.productPrice}>{product?.price} $</p> 
                                    <div className={style.buttons}>
                                        <div onClick={() =>{toggleFavs(product?.id)}} className={style.fav}><img src={user?.favProducts.some((i: IProduct) => {return i.id === product?.id}) ? RedLike : Like} alt="like" /></div>
                                        <button className={style.button} onClick={() =>{getToCart(id)}}>Add</button>
                                    </div>
                                </div>
                            </div>
            </div>
        </main>
    )
}

export default Product;


