import React, {ChangeEvent, useContext, useEffect, useState} from "react";
//@ts-ignore
import style from "./SearchBar.module.css";
import { routes } from "../../../utiles/routes";
import { CatalogueContext, FilterContext } from "../../../utiles/CatalogueContext";
import { IProduct } from "../../../utiles/UserContext";
import { Route, Routes, Link, useNavigate } from "react-router-dom";
import { ToggleData } from "../../../utiles/buttonTypes";


const SearchBar: React.FC<ToggleData> = (props) => {
    const toggleSearch = props.toggleS.toggleSearch; 
    //const setToggleSearch = props.toggleS.setToggleSearch;
    const {catalogue, setCatalogue} = useContext<any>(CatalogueContext);
    const {filteredTitle, setFilteredTitle} = useContext<Array<IProduct> | Array<[]> | any>(FilterContext);
    const [query, setQuery] = useState<string>('');
    const navigate = useNavigate();
    let filteredResult: Array<any | []> = [];
    const currentSearch = window.location.search.slice(3);

    const handleOnSubmit = () => { //ChangeEvent<HTMLButtonElement>
        console.error(query)
        if(query && query !== ''){
            if(catalogue.length > 0){
                filteredResult = catalogue.filter((i: IProduct) => {
                    let require = i?.title.toLowerCase();
                    if(require.includes(query.toLowerCase())) {
                        return i;
                         
                    }
                })
                setFilteredTitle(filteredResult);
                //setToggleSearch(false)
                return filteredResult;
            }
        }if(query === ''){
            setFilteredTitle([]);
            return filteredResult = [];
        }else{
            setFilteredTitle([]);
            return filteredResult = [];
        }
        
    }

    useEffect(() => {
        if(filteredResult && filteredResult?.length > 0){
           setFilteredTitle(filteredResult); 
        }
    }, filteredResult)

    console.log(toggleSearch)


    return (
        <form action="/" method="get">
            <label htmlFor="header-search">
                <span className={style.visuallyHidden}>Type</span>
            </label>
            <input
                type="text"
                id="header-search"
                placeholder="Search"
                name="s" 
                value={query}
                onChange={(e)=>setQuery(e.currentTarget.value)}
            />

            <button onClick={()=>{handleOnSubmit()}} type="submit">Search</button> 
    </form>
    )
};
    

export default SearchBar;